<template>
  <center>
  <h1>{{message}}</h1>
  <ul class="segmented-control">
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="1" name="option" id="option-1" checked v-on:click="prices">
      <label class="segmented-control__label" for="option-1">Prices</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="2" name="option" id="option-2" v-on:click="calendars">
      <label class="segmented-control__label" for="option-2">Calendars</label>
    </li>
    <li class="segmented-control__item">
      <input class="segmented-control__input" type="radio" value="3" name="option" id="option-3" v-on:click="news">
      <label class="segmented-control__label" for="option-3">News</label>
    </li>
   </ul>

   <div v-show="tab1">
   <h2>Focus</h2>
   <table>
   <tr v-for="row in focuses">
     <td>
       {{row.companyName}}
     </td>
     <td>
       {{row.latestPrice}}
     </td>
     <td>
       {{row.changePercent}}
     </td>
   </tr>
   </table>
   <h2>Active</h2>
   <table>
   </table>
   <h2>Gainer</h2>
   <table>
   </table>
   <h2>Loser</h2>
   <table>
   </table>
   </div>

   <div v-show="tab2">
   </div>

   <div v-show="tab3">
   </div>

  </center>
</template>


<script>
import Vue from 'vue';

export default {
  name: 'UsaMarket',
  data() {
    return {
      message: 'USA Stock Market',
      tab1: false,
      tab2: false,
      tab3: false,
      focuses: []
    }
  },

  created() {
    this.prices();
  },

  methods: {
    prices: function() {
      this.tab1 = true;
      this.tab2 = false;
      this.tab3 = false;
      const axios = require('axios');
      axios.get('https://api.iextrading.com/1.0/stock/market/list/infocus')
        .then((response) => {
          // handle success
          console.log(response.data);
          this.focuses = response.data;
        })
        .catch((error) => {
          // handle error
          console.log(error);
        })
        .then(() => {
          // always executed
        });
    },

    calendars: function() {
      this.tab1 = false;
      this.tab2 = true;
      this.tab3 = false;
    },

    news: function() {
      this.tab1 = false;
      this.tab2 = false;
      this.tab3 = true;
    }
  }
}
</script>



